import React, { Component } from 'react';

class TwoBigBanner extends Component {
    render() {
        return (
            <div id="bannerWrapper">
                    <div className="row" id="resizeMargin">
                        <div className="col-lg-12" id="resizePadding">
                            <div className="row" id="resizeBannerHerHim">
                                <div id="col-lg-6">
                                    <div className="bannerForHer">
                                        <img src="images/bannerforher.png" alt="" />
                                    </div>
                                </div>
                                <div id="col-lg-6">
                                    <div className="bannerForHim">
                                        <img src="images/bannerforhim.png" alt="" />
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
        );
    }
}

export default TwoBigBanner;