import * as types from './../constants/actionTypes'
import callApi from '../CallAPI/callApi';


export const actFetchAllProductsRequest = () => {
    return (dispatch) => {        
        return callApi('GET', 'products', null).then(res=>{
            dispatch(actFetchAllProducts(res.data))
        })
    }
}
export const actFetchAllProducts = (products) => {
    return {
        type: types.FETCH_ALL_PRODUCTS,
        products
    }
}

export const actProductDetailRequest = (id) => {
    return (dispatch) => {        
        return callApi('GET', `product/${id}`, null).then(res=>{
            dispatch(actProductDetail(res.data))
        })
    }
}
export const actProductDetail = (productDetail) => {
    return {
        type: types.FETCH_PRODUCT_DETAIL,
        productDetail
    }
}