import React, { Component } from 'react';
import { connect } from 'react-redux'
import * as actions from './../../actions/index'




class ProductDetail extends Component {

    componentDidMount() {
        let { match, fetchProductDetail } = this.props
        let id = match.params.id;

        fetchProductDetail(id)
    }


    render() {
        let { productDetail } = this.props;
        console.log(productDetail);

        return (

            <div id="content">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="productDetail">
                                {productDetail.map((productItem, index) => {
                                    return (
                                        <React.Fragment key={index}>
                                            <div className="left">
                                                <img src={productItem.image_urls[2]} alt="" />
                                            </div>
                                            <div className="right">
                                                <h4>{productItem.name}</h4>
                                                <p>{productItem.price} <span>đ</span></p>
                                                <div className="form-group" id="productColor">
                                                    <h5>Color</h5>
                                                    <select name="" id="">
                                                        {productItem.colors.map((color, index) => {
                                                            return <option key={index}>{color}</option>
                                                        })}
                                                    </select>
                                                </div>
                                                <div className="form-group" id="productSize">
                                                    <h5>Size</h5>
                                                    <select name="" id="">
                                                        {productItem.sizes.map((size, index) => {
                                                            return <option key={index}>{size}</option>
                                                        })}
                                                    </select>
                                                </div>
                                                <div className="form-group" id="productQuantity">
                                                    <h5>Quantity</h5>
                                                    <input type="number" name="" id="" aria-describedby="helpId" placeholder="1" min="1"
                                                        max="10" />
                                                </div>
                                                <div className="buy">
                                                    <a name="" id="" className="btn btn-primary" href="/" role="button"><i
                                                        className="fas fa-cart-plus"></i> Add to cart</a>
                                                    <a name="" id="" className="btn btn-warning" href="/" role="button"><i
                                                        className="fas fa-check"></i> Buy</a>
                                                </div>
                                            </div>
                                        </React.Fragment>
                                    )
                                })}
                            </div>

                            <div className="clear"></div>
                            <div id="description">
                                <ul className="nav nav-tabs" id="myTab" role="tablist">
                                    <li className="nav-item">
                                        <a className="nav-link active" id="home-tab" data-toggle="tab" href="#home" role="tab"
                                            aria-controls="home" aria-selected="true">Description</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" id="profile-tab" data-toggle="tab" href="#profile" role="tab"
                                            aria-controls="profile" aria-selected="false">Choose Size</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" id="contact-tab" data-toggle="tab" href="#contact" role="tab"
                                            aria-controls="contact" aria-selected="false">Contact</a>
                                    </li>
                                </ul>
                                <div className="tab-content" id="myTabContent">
                                    <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                                        {productDetail.map((productItem, index) => {
                                            return (
                                                <React.Fragment key={index}>
                                                    <p><span>Material</span>{productItem.description[0]}</p>
                                                    <p><span>Elasticities: </span>{productItem.description[1]}</p>
                                                    <p><span>Inner lining: </span>{productItem.description[2]}</p>
                                                    <p><span>Feature: </span>{productItem.description[3]}</p>
                                                    <p> {productItem.description[4]}</p>
                                                    <p> {productItem.description[5]}</p>
                                                    <p>{productItem.description[6]}</p>
                                                </React.Fragment>
                                            )
                                        })}

                                    </div>
                                    <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">...
                                </div>
                                    <div className="tab-pane fade" id="contact" role="tabpanel" aria-labelledby="contact-tab">...
                                </div>
                                </div>
                            </div>
                            <div id="productImages">
                                <div className="detailImage">
                                {productDetail.map((imgDetail, index)=>{
                                    return imgDetail.image_urls.map((img, index)=>{
                                        return <img src={img} alt="" />    
                                    })                                    
                                })}                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


const mapStateToProps = (state) => {
    return {
        productDetail: state.products
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        fetchProductDetail: (id) => {
            dispatch(actions.actProductDetailRequest(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetail);