import React, { Component } from 'react';

class TopMenu extends Component {
    render() {
        return (
            <div id="topMenu">
                <div className="container">
                    <div id="left">
                        <img src="/images/logo.png" alt="true" />
                    </div>
                    <div id="right">
                        <p>Miễn phí vận chuyển áp dụng với đơn hàng có giá trị từ 500k</p> <i className="fas fa-shopping-cart"></i>
                    </div>
                    <hr />
                </div>
            </div>
        );
    }
}

export default TopMenu;