import React, { Component } from 'react';

class NotFoundPage extends Component {
    render() {
        return (
            <div id="notFoundImg">
                <div class="container">
                    <h1>Page Not Found</h1>
                </div>
                <img src="/images/notfound.png" alt="true" />
            </div>
        );
    }
}

export default NotFoundPage;